export interface FetchResponse<T> extends Omit<Response, 'json'> {
    json: T;
}

interface FailParams {
    message: string,
    json: any
}

type IResolvHandler<T> = (json: T) => void;
type IFailHandler = (params: FailParams) => void;

export function getHeaders(): HeadersInit {
    return [
        ['Authorization', getAccessToken()],
        ['Content-Type', 'application/json'],
    ];
}

export function expectNoOk<T>(response: FetchResponse<T> | void,
    resolvHandler: IResolvHandler<T>,
    failHandler: IFailHandler
) {
    if (typeof response == 'undefined') {
        return;
    }

    const json = response.json;
    if (!response.ok) {
        const json = response.json;
        const textBody = JSON.stringify(json);
        const message = `http status ${response.status} ${response.statusText} ${textBody}`;
        failHandler({ message, json })
        return;
    }
    if (resolvHandler) resolvHandler(json);
}

